.section-title {
	font-family: 'BDStreetSignSans', sans-serif;
	color: $color_main;
	text-transform: uppercase;
	font-size: 45px;
	font-weight: 400;
	margin-bottom: 20px;
}

.section-desc {
	margin-bottom: 40px;
	font-size: 17px;
}

.section-linkmore {
	width: max-content;
	display: block;
	background: $color_main_2;
	color: #fff;
	padding: 15px 30px;
	font-size: 15px;
	border-radius: 150px;
	margin-bottom: 20px;
	font-weight: 700;
}

.section-hello {
	padding: 80px 0;

	.entry {
		color: #3C2415;
		margin-bottom: 30px;

		p {
			margin-bottom: 15px;
			font-size: 17px;
		}
	}

	.section-link {
		color: $color_main_2;
		font-weight: 700;
		font-size: 15px;

		img {
			margin-left: 10px;
		}

		&:not(:last-child) {
			margin-right: 43px;
		}
	}

	.box-img {
		width: 777px;
		aspect-ratio: 777/645;
	}
}

.section-trip {
	padding-bottom: 100px;
	position: relative;

	.container {
		max-width: 1000px;
	}

	.img-title {
		margin-bottom: 40px;
	}

	.inner {
		font-size: 15px;
		background: transparent;
		padding: 0;

		&-title {
			font-size: 17px;
			color: $color_main_2;
			font-weight: 700;
			background: $color_main;
			padding: 10px 16px;
			border-radius: 5px;
			margin: 0 auto 60px auto;
			width: max-content;
			position: relative;

			&::after {
				content: '';
				position: absolute;
				background: rgba($color: $color_main_2, $alpha: .4);
				width: 30px;
				height: 30px;
				border-radius: 50%;
				left: 50%;
				bottom: -55px;
				transform: translate(-50%, -50%);
			}

			&::before {
				content: '';
				position: absolute;
				background: $color_main_2;
				width: 18px;
				height: 18px;
				border-radius: 50%;
				left: 50%;
				bottom: -43px;
				transform: translate(-50%, -50%);
			}
		}

		ul {
			padding-left: 0;
			margin-bottom: 0;
		}
	}

	.item {
		position: relative;

		&:nth-child(3) {

			&::after {
				content: '';
				height: calc(100% + 50px);
				width: 50%;
				background: transparent;
				position: absolute;
				left: 0;
				top: 74px;
				border: 4px solid #3C2415;
				border-right: navajowhite;
				z-index: -1;
				border-top-left-radius: 25px;
				border-bottom-left-radius: 25px;
				transform: translateY(-5px);
			}
		}

		&:nth-child(4) {
			&::after {
				content: '';
				height: calc(100% + 47px);
				width: 50%;
				background: transparent;
				position: absolute;
				right: 0;
				top: 76px;
				border: 4px solid #3C2415;
				border-left: navajowhite;
				z-index: -1;
				border-top-right-radius: 25px;
				transform: translateY(-5px);
				border-bottom-right-radius: 25px;
			}
		}

		&:not(:last-child) {
			margin-bottom: 44px;
		}
	}

	&::before {
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		background: url(/images/ship-bg.png) no-repeat;
		background-size: cover;
		width: 488px;
		height: 443px;
	}

	&::after {
		content: '';
		position: absolute;
		right: 0;
		top: 0;
		background: url(/images/ship-bg2.png) no-repeat;
		width: 488px;
		height: 440px;
	}
}

.inner {
	&-title {
		font-weight: 700;
		color: #000;
		margin-bottom: 15px;
		font-size: 20px;
		text-align: center;
	}

	&-desc {
		font-size: 17px;
		margin-bottom: 0;
		text-align: center;
	}

	&-icon {
		width: 150px;
		height: 150px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 auto 20px auto;
	}

	&-bg {
		background: url(/images/bg-inner.png) no-repeat;
		background-size: cover;
	}

	&-img {
		width: 100%;
		aspect-ratio: 360/265;
		border-radius: 15px;
		overflow: hidden;
		margin-bottom: 20px;

		img {
			object-fit: cover;
			object-position: 50% 50%;
			-o-object-position: 50% 50%;
			width: 100%;
			height: 100%;
		}
	}
}

.section-inherit {
	padding-bottom: 80px;

	.inner {
		background: #F2E9D7;
		border-radius: 16px;
		height: 100%;
		padding: 30px 20px;
	}
}

.section-work {
	padding-bottom: 80px;

	.inner {
		padding: 0;
		background: transparent;

		&-desc {
			text-align: left;
		}
	}
}

.section-sick {
	padding-bottom: 80px;

	.inner {
		&-icon {
			width: 100px;
			height: 100px;
			margin-bottom: 15px;

			img {
				width: 60px;
				height: 60px;
			}
		}

		&-title {
			margin-bottom: 0;
			text-align: center;
		}
	}
}

.section-why {
	padding-top: 150px;
	padding-bottom: 80px;
	margin-bottom: 80px;
	font-size: 17px;
	background: url(/images/bg-why.png) no-repeat;
	background-size: cover;
	position: relative;

	.section-title {
		line-height: 1.4;
	}

	.list-why-text {
		margin-bottom: 44px;
	}

	.inner-mess {
		display: flex;
		margin-bottom: 15px;
		align-items: center;

		&-avatar {
			flex-shrink: 0;
			width: 60px;
			height: 60px;
			border-radius: 50%;
			overflow: hidden;
			margin-right: 12px;

			img {
				object-fit: cover;
				object-position: 50% 50%;
				-o-object-position: 50% 50%;
				width: 100%;
				height: 100%;
			}
		}

		&-text {
			color: $color_main;
			font-weight: 700;
			font-size: 20px;
			margin-bottom: 0;
		}
	}

	.box-why-img {
		display: flex;
	}

	.box-img {
		border-radius: 15px;
		overflow: hidden;

		img {
			object-fit: cover;
			object-position: 50% 50%;
			-o-object-position: 50% 50%;
			width: 100%;
			height: 100%;
		}

		&:nth-child(1) {
			margin-bottom: 10px;
			width: 100%;
			aspect-ratio: 564/416;
		}

		&:nth-child(2) {
			margin-right: 5px;
		}

		&:nth-child(3) {
			margin-left: 5px;
		}

		&:nth-child(2),
		&:nth-child(3) {
			width: 49%;
			aspect-ratio: 277/204;
			flex-shrink: 0;
		}
	}

	.box-item {
		display: flex;
		flex-wrap: wrap;

		&:first-child {
			margin-right: 5px;
		}

		&:last-child {
			margin-left: 5px;
			flex-flow: wrap-reverse;

			.box-img {
				&:nth-child(1) {
					margin-bottom: 0;
				}

				&:nth-child(2),
				&:nth-child(3) {
					margin-bottom: 10px;
				}
			}
		}
	}

	&::before {
		content: '';
		position: absolute;
		background: url(/images/caydua.png) no-repeat;
		background-size: cover;
		width: 300px;
		height: 513px;
		left: 0;
		top: 40%;
		transform: translateY(-50%);
	}
}

.section-form {
	position: relative;
	padding-bottom: 80px;

	.box-contact {
		margin-bottom: 40px;
	}

	.box-info-home {
		background: #F1E9D7;
		border-radius: 15px;
		padding: 35px 40px;

		.box-title {
			font-size: 17px;
			font-weight: 700;
			margin-bottom: 13px;
		}

		.box-info {
			display: flex;
			align-items: baseline;
			margin-bottom: 0;

			img {
				margin-right: 12px;
			}

			&:not(:last-child) {
				margin-bottom: 10px;
			}
		}

		.box-note {
			color: $color_main_2;
			font-size: 17px;
			margin-bottom: 0;
			font-style: italic;
		}
	}

	&::after {
		content: '';
		position: absolute;
		background: url(/images/caydua2.png) no-repeat;
		background-size: cover;
		width: 300px;
		height: 758px;
		right: 0;
		top: -160px;
	}
}

.section-stories {
	margin-bottom: 80px;

	.list-stories {
		margin-bottom: 0;
	}
}

.post {
	&-thumbnail {
		width: 100%;
		aspect-ratio: 263/194;
		border-radius: 10px;
		overflow: hidden;
		margin-bottom: 15px;
		display: block;

		img {
			object-fit: cover;
			object-position: 50% 50%;
			-o-object-position: 50% 50%;
			width: 100%;
			height: 100%;
		}
	}

	&-title {
		font-size: 17px;
		font-weight: 700;
	}

	&-big {
		.post-thumbnail {
			aspect-ratio: 555/409;
		}

		.post-title {
			font-size: 24px;
			margin-bottom: 15px;
		}
	}

	&-desc {
		color: #777777;
		font-size: 17px;
	}
}

.section-diary {
	padding-bottom: 80px;
	position: relative;

	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		background: url(/images/ship-bg-diary.png) no-repeat;
		background-size: cover;
		width: 340px;
		height: 416px;
	}
}

.list-post {
	margin-bottom: 40px;

	.item {
		&:not(:nth-child(3)):not(:nth-child(4)) {
			margin-bottom: 30px;
		}
	}
}

.box-loadmore {
	text-align: center;
	font-weight: 700;

	a {
		color: $color_main_2;
	}
}

.section-diary2 {
	padding-bottom: 80px;

	.list-post {
		margin-bottom: 0;
	}

	.item {
		margin-bottom: 30px;
	}

	.list-tag {
		margin-bottom: 40px;
		display: flex;
		align-items: center;
		justify-content: center;

		.tag-item {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 181px;
			height: 52px;
			background: url(/images/bg-tag-none.png) no-repeat;
			background-size: contain;
			color: $color_main;
			font-size: 14px;
			font-weight: 700;

			&.active {
				background: url(/images/bg-tag.png) no-repeat;
				background-size: contain;
			}

			&:not(:last-child) {
				margin-right: 20px;
			}
		}
	}
}

.section-social {
	background-image: url(/images/bg-social2.png), url(/images/bg-social.png);
	background-repeat: no-repeat, no-repeat;
	background-position: 100% 100%, 0 20%;
	min-height: 1020px;

	.container {
		max-width: 1555px;
	}

	.box-content {
		height: 100%;
		display: flex;
		flex-flow: column;
		justify-content: center;

		.section-desc {
			width: 70%;
		}
	}

	.list-box-social {
		display: flex;
	}

	.box-social {
		background: #fff;
		padding: 10px;
		border-radius: 10px;
		padding-bottom: 22px;

		&-img {
			width: 100%;
			aspect-ratio: 243/140;
			margin-bottom: 15px;

			img {
				object-fit: cover;
				object-position: 50% 50%;
				-o-object-position: 50% 50%;
				width: 100%;
				height: 100%;
			}
		}

		&-title {
			font-size: 15px;
			font-weight: 700;
			margin-bottom: 10px;
		}

		&-role {
			height: 20px;
			margin-bottom: 15px;
			width: 164px;

			img {
				object-fit: cover;
				object-position: 50% 50%;
				-o-object-position: 50% 50%;
				width: 100%;
				height: 100%;
			}
		}

		&-link {
			background: #0866FF;
			padding: 10px 20px;
			border-radius: 10px;
			overflow: hidden;
			color: #fff;
			font-size: 15px;
			font-weight: 700;
			width: max-content;
			margin: 0 auto;
			display: block;

			img {
				margin-right: 10px;
			}
		}

		&:not(:last-child) {
			margin-right: 30px;
		}
	}
}

.section-mess {
	padding-bottom: 80px;
	position: relative;

	.box-img {
		margin-bottom: 24px;
	}

	.section-linkmore {
		margin: 0 auto;
	}

	&::before {
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		background: url(/images/ship-bg.png) no-repeat;
		background-size: cover;
		width: 488px;
		height: 443px;
	}

	&::after {
		content: '';
		position: absolute;
		right: 0;
		top: 0;
		background: url(/images/ship-bg2.png) no-repeat;
		width: 488px;
		height: 440px;
	}
}